.navContainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  padding: 2%;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
}

.navContainer .logo {
  font-weight: bolder;
}

.navContainer .logo li {
  list-style: none;
}

.navContainer .logo li a {
  text-decoration: none;
  color: black;
}

.navContainer .navPages {
  display: -ms-grid;
  display: grid;
  grid-auto-flow: column;
  -webkit-column-gap: 12px;
          column-gap: 12px;
}

.navContainer .navPages li {
  list-style: none;
}

.navContainer .navPages li a {
  text-decoration: none;
  color: black;
}
