.navContainer {

    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    padding: 2%;
    justify-content: space-around;
    width: 100%;

    .logo {
        font-weight: bolder;

        li {
            list-style: none;

            a {
                text-decoration: none;
                color: black;
            }
        }
    }

    .navPages {
        display: grid;
        grid-auto-flow: column;
        column-gap: 12px;

        li {
            list-style: none;

            a {
                text-decoration: none;
                color: black;
            }
        }

    }
}